body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.example {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #2f2f37;
  max-width: 960px;
  padding: 16px;
}

.example code {
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
  background-color: #ff6b0a;
  color: #fff;
  padding: 2px 6px;
  border-radius: 2px;
}

.example .background {
  height: 240px;
  // ❗️❗️❗️ The path to images from a SASS file
  // is always the same as from the css folder.
  background-image: url('../images/logo.png');
  background-size: cover;
  background-position: center;
}

.card-holder {
  flex-grow: 1;
}

//=========card===========
.film__item {
  max-width: 280px;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    max-width: 336px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 395px;
  }

  & img {
    width: 280px;
    height: 402px;
    border-radius: 5px;
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      margin-bottom: 8px;
      width: 336px;
      height: 455px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 12px;
      width: 395px;
      height: 574px;
    }
  }

  & h2 {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.17;
    text-transform: uppercase;
    color: #000000;

    @media screen and (min-width: 768px) {
      line-height: 1.33;
    }

    @media screen and (min-width: 1280px) {
      font-size: 20px;
      line-height: 1.2;
    }
  }

  & p {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    color: #ff6b08;

    @media screen and (min-width: 1280px) {
      font-size: 20px;
      line-height: 1.2;
    }
  }
}

//=========footer=========
.footer {
  background-color: #f7f7f7;

  & .container {
    padding: 30px 0;
    text-align: center;

    @media screen and (min-width: 767px) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  & p {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.14;

    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: 1.19;
    }

    color: #545454;
  }

  & p:first-child {
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      margin-bottom: 0;
    }
  }

  & p:last-child {
    display: inline-flex;
    align-items: center;

    @media screen and (min-width: 768px) {
      margin-left: 5px;
      display: flex;
    }
  }
}

.footer__icon {
  margin: 0 5px;
}

.footer__link {
  margin-left: 5px;
  text-decoration: underline;
  cursor: pointer;
}
